@import url("https://cdn.jsdelivr.net/npm/bootstrap-icons@1.11.3/font/bootstrap-icons.min.css");
* {
  box-sizing: border-box;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 0;
  width: 100%;
  min-height: 100vh;

  background-color: #6d9ac4;
  box-sizing: border-box;
}
#root {
  min-height: 100vh;
  height: 100%;
}
.App {
  min-height: 100vh;
  height: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  /* flex-direction: column; */
}

.card {
  width: 371px;
  height: 551px;
  /* border: 1px solid red; */
  border-radius: 11px;
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* justify-content: center; */
  color: #494949e0;
  padding: 21px;
  box-shadow: 1px 5px 5px 4px #00000054;
}
p {
  width: 85%;
  font-weight: 500;
}
.form {
  width: 85%;
}
label {
  margin-bottom: 1.5px;
}
input {
  outline: none;
  width: 100%;
  height: 41px;
  /* border-style: solid grey; */
  border-radius: 5px;
  border: 1.5px solid rgba(122, 120, 120, 0.518);
  margin-top: 5px;
}
input:nth-of-type(1) {
  margin-bottom: 21px;
}
.card button {
  outline: none;
  width: 85%;
  height: 41px;
  border-style: solid;
  border-radius: 7px;
  background-color: #ed5784;
  color: #ffffffbe;
  outline: none;
  border: none;
  margin-top: 21px;
  box-shadow: 0px 1px 7px 1px #00000054;
  font-weight: 400;
  letter-spacing: 1.5px;
  font-size: 15px;
}

.OR {
  display: flex;
  align-items: center;
  width: 85%;
  margin: 21px 0;
}
.line1 {
  border-bottom: 1px solid rgba(122, 120, 120, 0.518);
  width: 51%;
  height: 1px;
}
.line2 {
  border-bottom: 1px solid rgba(122, 120, 120, 0.518);
  width: 51%;
  height: 1px;
}
.or {
  border: 1px solid rgba(122, 120, 120, 0.518);
  padding: 3px;
  border-radius: 5px;
}

.social {
  width: 45%;
  display: flex;
  justify-content: space-between;
}

.bi-google {
  color: #c01b1c;
  border: 3px solid #c01b1c;
  padding: 2px 7px;
  border-radius: 50%;
  font-size: 19px;
}
.bi-facebook {
  color: #4b66a0;
  border: 3px solid #4b66a0;
  padding: 2px 7px;
  border-radius: 50%;
  font-size: 19px;
}
.bi-linkedin {
  color: #3c66a4;
  border: 3px solid #3c66a4;
  padding: 3px 7px;
  border-radius: 50%;
  font-size: 19px;
}
.check {
  display: flex;
  align-items: center;
  margin-top: 7px;
  width: 85%;
}
input[type="checkbox"] {
  height: 21px;
  width: 21px;
  background-color: #ed5784;
  accent-color: #ed5784;
  margin: 0;
  margin-right: 11px;
}
span {
  width: 85%;
  text-align: end;
  margin-top: 5px;
}
.bottom {
  width: 85%;
  text-align: center;
  cursor: pointer;
}

/* second task */
.secondTask {
  background-color: #ffffff;
  width: 301px;
  height: 201px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  border: 3px solid rgb(158, 158, 158);
}
.plusMinus {
  display: flex;
  width: 151px;
  border: 1px solid grey;
}

.mp {
  color: #1486af;
  font-size: 31px;
  width: 33.3%;
  display: flex;
  justify-content: center;
  align-items: center;
  /* display: flex; */
  font-weight: 600;
  cursor: pointer;
  border: none;
  background: none;
}
.number {
  color: #020a0c;
  font-size: 21px;
  width: 33.3%;
  text-align: center;
  vertical-align: middle;
  /* display: flex; */
  font-weight: 600;
  display: flex;
  justify-content: center;
  align-items: center;
}
.mp.active,
.mp.active1 {
  background: lightgrey !important;
  opacity: 0.5;
}
